import React, {useState, useEffect} from 'react';
import {Link} from "react-router-dom"
import { Table, Button, Tag, Input, Space, Checkbox, Menu } from 'antd';
import { SearchOutlined, CloseCircleOutlined, FilterFilled, MenuFoldOutlined } from '@ant-design/icons';

import TableGroups from './../../../table-groups/table-groups.js'
import { getColumn } from './../change-column/change-column.js'
import Pagination from './../pagination/pagination.js'

import localStorage_change from './../../../../#localStorage_change.js'
// Style
import styled from '@emotion/styled';


import Url from './../../../../url/url.js'

const Create_Columns_Rows_Groups = () => {

	const [offsetG, setOffsetG] = useState(0)
	const [Count, setCount] = useState(1)

	const [dataObj, setDataObj] = useState([])

	// Filters
	const [loadingButtomFilrer, setLoadingButtomFilrer] = useState(false)
	const [filterDropdownVisible, setFilterDropdownVisible] = useState({
	    GROUP_NAME: false,
	})

	const [GROUP_NAME, setGROUP_NAME] = useState('')
	// /Filters

	// Change colums
	const [g, setG] = useState([
		<span style={{ paddingLeft: "7px" }}>Группа</span>,
		"GROUP_NAME",
	])
	const [l, setL] = useState([
		<span style={{ paddingLeft: "7px" }}>Уровень группы</span>,
		"LEVEL",
	])
	const [p, setP] = useState([
		<span style={{ paddingLeft: "7px" }}>Количество мест</span>,
		"PLACES",
	])
	const [o, setO] = useState([
		<span style={{ paddingLeft: "7px" }}>Свободных мест</span>,
		"OCCUPIED",
	])
	const [n, setN] = useState([
		<span style={{ paddingLeft: "7px" }}>Число учеников</span>,
		"NUMBER_PUPILS",
	])
	const [t, setT] = useState([
		<span style={{ paddingLeft: "7px" }}>Учитель</span>,
		"TUTOR",
	])
	// /Change colums

	const axios = require('axios'); // AJAX

	const [Tutors, setTutors] = useState([])

	const addTUTOR = () => {
		axios({
			method: 'get',
			url: `${Url}/CRMAccounts?limit=100&offset=0&role=teacher`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			}
		})
		.then((res) => {
			let {data} = res
			setTutors(data)
		})
		.catch(error => {
			if (error.response) {
				let { status, data, statusText } = error.response;
				if (status == 403) {
					if (data.message == "TOKEN_EXPIRED") {
						localStorage_change(error.response.data.token);
						addTUTOR()
					} else {
						localStorage.removeItem("tokenID");
						window.location.replace("/");
					}
				}
			}
		})
	}

	const update = (offset = 0, filters = {}) => { // console.log(filters)

		setLoadingButtomFilrer(true)
		
		let box = {}

		for (let key in filters) {
			if (key != 'OCCUPIED' & filters[key][0] != undefined) {
				console.log(filters[key], 1)
				box[`${key}S`] = filters[key]
			} else if (filters[key].length) {
				console.log(filters[key], 2)
				box[key] = filters[key][0]
			}
		}

		console.log(box)

		axios({
			method: 'post',
			url: `${Url}/CRM/Groups/find?limit=10&offset=${offset}`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			},
			data: {
				filters: box
			}
		})
		.then(res => {
			let {data, headers} = res
			setDataObj(data)
			setFilterDropdownVisible({GROUP_NAME: false})
			setLoadingButtomFilrer(false)
			setCount(headers.count);
		})
		.catch((error) => {
			if (error.response) {
				setLoadingButtomFilrer(false)
				let { status, data } = error.response;

				if (status == 403) {
					if (data.message == "TOKEN_EXPIRED") {
						localStorage_change(data.token);
						update()
					} else {
						localStorage.removeItem("tokenID");
						window.location.replace("/");
					}
				}
			}
		})
	}

	useEffect(() => {
		addTUTOR()
		update()
	}, [])

	// Filrers
	const [paramFilrers, setParamFilrers] = useState({ // paramFilrers
		GROUP_NAME: [],
		LEVEL: [],
		TUTOR: [],
		OCCUPIED: []
	});

	console.log(paramFilrers)

	let CheckboxValue = {
	    GROUP_NAME: {}
	};

	for (let key in CheckboxValue) {
	    paramFilrers[key].forEach((item) => {
	        CheckboxValue[key][item] = true;
	    });
	}

	// const [dataGroupsChecked, setDataGroupsChecked] = useState({})
	// // { // delete obj['gh']
	// // 	id: ['text', true],
	// // 	kg: ['java', true]
	// // })
	// const [GroupsCheckedF, setGroupsCheckedF] = useState({})

	// let heapGroups = {...dataGroupsChecked, ...GroupsCheckedF}

	// let dataGroupsCheck = []

	// for (let key in heapGroups) {

	// 	dataGroupsCheck.push({
	// 		id: key,
	// 		text: heapGroups[key][0],
	// 		checked: heapGroups[key][1]
	// 	})
	// }


	const [globalFilters, setGlobalFilters] = useState({
	    GROUP_NAME: []
	});

	const handleSearch = (selectedKeys, confirm, dataIndex) => {

	    if (selectedKeys[0]) {
	    	let FiltersLocal = { ...globalFilters };

	    	FiltersLocal[dataIndex].push({
	    		text: selectedKeys[0],
	    		value: selectedKeys[0],
	    	});

	    	setGlobalFilters(FiltersLocal);
	    	selectedKeys[0] = ''
	    }
	};

	let DataSource = dataObj.map(item => {

		let {GROUP_NAME, LEVEL, PLACES, PUPILS, TUTOR, _id} = item

		return ({
			key: _id,
			GROUP_NAME: [GROUP_NAME, _id],
			LEVEL: LEVEL,
			PLACES: PLACES,
			NUMBER_PUPILS: PUPILS.length,
			TUTOR: TUTOR? `${TUTOR.surname} ${TUTOR.name} ${TUTOR.midname}` : '-',
			OCCUPIED: PLACES - PUPILS.length
		})
	})

	const BoxButtom = styled.div({
	    marginTop: "10px",
	    svg: {
	        marginBottom: "3px",
	    },
	});

	const getColumnSearchProps = (dataIndex) => ({
	    filterDropdown: ({
	        setSelectedKeys,
	        selectedKeys,
	        confirm,
	        clearFilters,
	    }) => (
	        <div style={{ padding: 8 }}>
	            <Input
	                placeholder={`Создать фильтр ${dataIndex}`}
	                value={selectedKeys[0]}
	                onChange={(e) =>
	                    setSelectedKeys(e.target.value ? [e.target.value] : [])
	                }
	                onPressEnter={() =>
	                    handleSearch(selectedKeys, confirm, dataIndex)
	                }
	                style={{ marginBottom: 8, display: "block" }}
	            />
	            <Space>
	                <Button
	                    type="primary"
	                    onClick={() => {
	                        handleSearch(selectedKeys, confirm, dataIndex);
	                    }}
	                    // onClick={() => {
	                    // 	confirm({ closeDropdown: false })

	                    // 	setSelectedKeys(selectedKeys[0])
	                    // 	setSearchedColumn(dataIndex)
	                    // }}
	                    size="small"
	                    style={{ width: 140 }}
	                >
	                    Добавить в фильтр
	                </Button>
	                <Button
	                    type="link"
	                    size="small"
	                    // icon={<SearchOutlined />}
	                    onClick={() => {
	                        let filters = { ...globalFilters };
	                        filters[dataIndex] = [];
	                        // setGlobalFilters(filters);
	                        setGlobalFilters(filters)

	                        let dataFilters = { ...paramFilrers };
	                        dataFilters[dataIndex] = [];
	                        // setParamFilrers(dataFilters);

	                        setParamFilrers(dataFilters)

	                        update(0, dataFilters);
	                    }}
	                >
	                    Очистить фильтр
	                </Button>
	            </Space>
	            <Menu>
	                {globalFilters[dataIndex].map((item, i) => {
	                    let checked = false;
	                    paramFilrers[dataIndex].forEach((param) => {
	                        if (param == item.text) {
	                            checked = true;
	                        }
	                    });

	                    return (
	                        <Menu.Item key={`${item.text}${i}`}>
	                            <Checkbox
	                                defaultChecked={checked}
	                                onChange={(e) => {
	                                    if (e.target.checked) {
	                                        CheckboxValue[dataIndex][
	                                            item.text
	                                        ] = true;
	                                    } else {
	                                        CheckboxValue[dataIndex][
	                                            item.text
	                                        ] = false;
	                                    }
	                                }}
	                            >
	                                {item.text}
	                            </Checkbox>
	                        </Menu.Item>
	                    );
	                })}
	            </Menu>
	            <BoxButtom>
	                <Button
	                    loading={loadingButtomFilrer}
	                    type="primary"
	                    size="small"
	                    icon={<SearchOutlined />}
	                    onClick={() => {
	                        let paramLocal = []; //...paramFilrers[dataIndex]

	                        for (let key in CheckboxValue[dataIndex]) {
	                            if (CheckboxValue[dataIndex][key]) {
	                                paramLocal.push(key);
	                            }
	                        }
	                        let data = { ...paramFilrers };
	                        data[dataIndex] = paramLocal;

	                        // setParamFilrers(data);

	                        setParamFilrers(data)
	                        setGlobalFilters(globalFilters)

	                        update(0, data);
	                    }}
	                >
	                    Применить фильтр
	                </Button>
	            </BoxButtom>
	        </div>
	    ),
	    filterIcon: () => {
	        let color = undefined;
	        if (paramFilrers[dataIndex][0]) {
	            color = "#1890ff";
	        }

	        if (filterDropdownVisible[dataIndex]) {
	            return <CloseCircleOutlined />;
	        } else {
	            return (
	            	<>
	            		<SearchOutlined style={{ color: color }} />
	            	</>
	            )
	        }
	    },
	    onFilterDropdownVisibleChange: (Visible) => {
	        let per = { ...filterDropdownVisible };
	        per[dataIndex] = Visible;
	        setFilterDropdownVisible(per);

	        if (!Visible) {
		        setParamFilrers(paramFilrers)
		        setGlobalFilters(globalFilters)
	    	}
	    },
	    filterDropdownVisible: filterDropdownVisible[dataIndex],
	});
	// /Filrers

	let filtersTutors = Tutors.map(item => ({
		text: `${item.surname} ${item.name}`,
		value: item._id
	}))

	let Columns = [
		{
			...getColumn(g, setG, 'Группа', 'GROUP_NAME'),
			key: 'GROUP_NAME',
			render: (GROUP_NAME) => {
			    return <Link to={`/group/${GROUP_NAME[1]}`}>{GROUP_NAME[0]}</Link>
			},
			...getColumnSearchProps("GROUP_NAME")
		},
		{
			...getColumn(l, setL, 'Уровень группы', 'LEVEL'),
			key: 'LEVEL',
			filters: [
	            {
	                text: "1",
	                value: 1,
	            },
	            {
	                text: "2",
	                value: 2,
	            },
	            {
	                text: "3",
	                value: 3,
	            },
	            {
	                text: "4",
	                value: 4,
	            }
	        ],
	        defaultFilteredValue: paramFilrers.LEVEL.map(item => item + '')
		},
		{
			...getColumn(p, setP, 'Количество мест', 'PLACES'),
			key: 'PLACES'
		},
		{
			...getColumn(o, setO, 'Свободных мест', 'OCCUPIED'),
			key: 'OCCUPIED',
			filters: [
	            {
	                text: "Заполненно",
	                value: true,
	            },
	            {
	                text: "Свободно",
	                value: false,
	            },
	        ],
	        defaultFilteredValue: paramFilrers.OCCUPIED.map(item => `${item}`)  // ...paramFilrers.OCCUPIED
		},
		{
			...getColumn(n, setN, 'Число учеников', 'NUMBER_PUPILS'),
			key: 'NUMBER_PUPILS'
		},
		{
			...getColumn(t, setT, 'Учитель', 'TUTOR'),
			key: 'TUTOR',
			filters: filtersTutors,
			defaultFilteredValue: [...paramFilrers.TUTOR]
		}
	]

	const Box = styled.div({
		padding: '0 30px'
	})

	return (
		<Box>
			<TableGroups 
				dataSource={DataSource} 
				columns={Columns}
				setParamFilrers={setParamFilrers}
				paramFilrers={paramFilrers}
				update={update}/>
			<Pagination getItem={update} count={Count} offset={offsetG} setOffset={setOffsetG}/>
		</Box>
	)
}

export default Create_Columns_Rows_Groups