import React, {useState, useEffect} from 'react';
import { Table } from 'antd';

import Url from './../../url/url.js';
import { getColumn } from './../home-page/#more-functions/change-column/change-column.js'

import { SearchOutlined, CloseCircleOutlined, FilterFilled, MenuFoldOutlined } from '@ant-design/icons';

import styled from '@emotion/styled';

const Table_Pupils = ({
		paramFilrers,
		setParamFilrers,
		globalFiltersG,
		setGlobalFiltersG,

		dataGroupsChecked,
		setDataGroupsCheckedG,

		dataSource,
		update,
		loadingButtomFilrer,
		setLoadingButtomFilrer,
		filterDropdownVisible,
		setFilterDropdownVisible,
		columns,

		scrollLeft
	}) => {

	useEffect(() => {
		document.querySelector('.ant-table-content').scrollLeft = scrollLeft
	})

	const axios = require('axios'); // AJAX

	const Box = styled.div({
		// width: '1515px',
		'th, td': {
			padding: '16px 11px'
		}
	})

	return (
		<Box>
			<Table
			    pagination={false}
			    dataSource={dataSource}
			    columns={columns}
			    scroll={{ x: 0 }}
			    // width={1515}
			    onChange={(pagination, filters, sorter, extra) => {
			    	let box = {...paramFilrers}
			    	if (filters.gender) {
			    		box.gender = filters.gender
			    	} else {
			    		box.gender = []
			    	}

			    	if (filters.balance) {
			    		box.balance = filters.balance
			    	} else {
			    		box.balance = []
			    	}

			    	setParamFilrers(box)
			    	update(box, dataGroupsChecked)
			    }}
			/>
		</Box>
	)
}

export default Table_Pupils