import React, {useState, useEffect} from 'react'
import {BrowserRouter as Router, Route, Switch, Link} from "react-router-dom"

import { Layout, Menu } from 'antd'
import {
	LogoutOutlined,
	TeamOutlined,
	UserOutlined,
	SearchOutlined,
	DatabaseOutlined
} from '@ant-design/icons'

import SearchAll from './../search/search.js'

import Student from './student/student.js'
	import StudentPage from './student/pageStudent/pageStudent.js'

import Group from './group/group.js'
	import GroupPage from './group/group-page/group-page.js'

// Сводные таблицы
import Create_Columns_Rows_Groups from './#more-functions/сreate-columns-and-rows/сreate-columns-rows-groups.js'
import Create_Columns_Rows_Pupils from './#more-functions/сreate-columns-and-rows/сreate-columns-rows-pupils.js'

import styled from '@emotion/styled'

const HomePage = ({logOut}) => {

	const [pageGruop, setPageGruop] = useState(['0', '0']);
	const [pageStudent, setPageStudent] = useState('1');

	// const breakpoints = [379, 700, 1024];

	// const mq = breakpoints.map(
	// 	bp => `@media (max-width: ${bp}px)`
	// );

	// const BoxStudent = styled.div({
	// 	marginLeft: '100px',
	// 	overflowY: 'auto',
 //   	 	height:'100vh',
 //   	 	paddingBottom: '100px',
 //   	 	'&::-webkit-scrollbar': {
 //   	 		height: '5px',
 //   	 		backgroundColor:'rgba(0, 0, 0, 0.5)',
 //   	 		borderRadius: '10px'
 //   	 	},
 //   	 	'&::-webkit-scrollbar-thumb': {
 //   	 		backgroundColor: '#17a2b8',
 //   	 		borderRadius: '10px'
 //   	 	},
	// 	[mq[1]]: {
	// 		marginLeft: '0px'
	// 	}
	// });

	
	const { Header, Sider } = Layout;
	const { SubMenu } = Menu;

	const [collapsed, setCollapsed] = useState(true)

	const onCollapse = collapsed => {
		setCollapsed(collapsed);
	};

	const Vag = styled.div({
		'.ant-menu': {
			height: '100vh',
			position: 'relative'
		},
		'.vag123': {
			position: 'absolute',
			bottom: '48px',
			width: collapsed? '80px' : ''
		},
		'.site-layout': {
			padding: '15px'
		},
		'& > section': {
			'& > aside': {
				position: 'fixed',
				zIndex: 99
			},
			'& > section': {
				marginLeft: collapsed? '80px' : '200px'
			}
		}
		// 'ul.ant-menu': {
		// 	position: 'fixed',
		// 	width: '200px'
		// }
	})

	const Box = styled.div({
		overflow: 'auto' 
	})

	// if (window.location.pathname != '/' & document.readyState == 'loading') {
	// 	window.location.replace("/");
	// }
	
	return (
		<>
		<Router>
		<Vag>
		  <Layout style={{ minHeight: '100vh' }}>
	        <Sider collapsible collapsed={collapsed} onCollapse={onCollapse}>
	          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
	          		<Menu.Item key="Search" icon={<SearchOutlined />}>
				        <SearchAll setPageStudent={setPageStudent} setPageGruop={setPageGruop}/>
			        </Menu.Item>
			        <Menu.Item key="2" icon={<UserOutlined />}>
				        <Link to='/student'>
				        	Ученики
				        </Link>
			        </Menu.Item>
			        <Menu.Item key="3" icon={<TeamOutlined />}>
				        <Link to='/group'>
				        	Группы
				        </Link>
			        </Menu.Item>
			        <SubMenu key="sub1" icon={<DatabaseOutlined />} title="Сводные таблицы">
		              <Menu.Item key="sub-student">
		              	<Link to='/table-pupils'>
				        	Ученики
				        </Link>
				      </Menu.Item>
		              <Menu.Item key="sub-groups">
		              	<Link to='/table-groups'>
				        	Группы
				        </Link>
		              </Menu.Item>
		            </SubMenu>
			        <Menu.Item 
			            	onClick={() => {
			            		localStorage.removeItem('tokenID');
			            		logOut()
			            	}}
			            	id='LogoutOut'
			            	key="Logout" 
			            	className='vag123' 
			            	icon={<LogoutOutlined />}>
				            <Link to='/'>
				            	Выход
				            </Link>
			        </Menu.Item>
	          </Menu>
	        </Sider>
	        <Layout className="site-layout">
	          <Switch>
		          	  <Route exact path='/student' component={() => <Student setPageStudent={setPageStudent}/>}/>
			          	<Route path={`/student/:id`} children={() => <StudentPage setUrlPage={setPageGruop} />}/>
			          
			          <Route exact path='/group' component={() => <Group setPageGruop={setPageGruop}/>}/>
				      	<Route path={`/group/:id`} children={() => <GroupPage setUrlPage={setPageStudent} />}/>

				    {/*Сводные таблицы*/}
				      <Route exact path='/table-groups' component={() => <Create_Columns_Rows_Groups />}/>

				      <Route exact path='/table-pupils' component={() => (
				      		<Box>
				      			<Create_Columns_Rows_Pupils />
				      		</Box>)}/>
				      
				      <Route path="/student/*"> 
			          	<h2>Странца не найдена</h2>
		          	  </Route>
	          </Switch>
	        </Layout>
	      </Layout>
	    </Vag>
	    </Router>
		</>
	);
};

export default HomePage;