import moment from 'moment';

const ReturnAge = (data) => {

	let year = moment(data).format('YYYY'), // , 'YYYY-MM-DD'
	    month = moment(data).format('MM'), // , 'YYYY-MM-DD'
	    day = moment(data).format('DD'); // , 'YYYY-MM-DD'
	
	let age = moment().diff(`${year}-${month}-${day}`, 'years')   

	return age? age : 'Не указан';
}

export default ReturnAge;